import { IWebinarCard } from '../../../interfaces';
import IVFWebinarCard from '../../molecules/IVFWebinarCard/IVFWebinarCard';

interface Props {
  webinarList: IWebinarCard[];
  activeFilter: string;
}

export default function IVFAcademyListGroup(props: Props) {
  const { activeFilter, webinarList } = props;
  const filteredFinal = webinarList.filter(
    (item) => item.date === activeFilter
  );

  return (
    <section className="px-6 lg:px-24">
      <div className="mt-3 grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-4">
        {filteredFinal.map((webinar) => (
          <IVFWebinarCard
            key={webinar.id * Math.random()}
            ctaColor="primary"
            webinarCard={webinar}
          />
        ))}
      </div>
    </section>
  );
}
