import Button from '../../atoms/Button';
import DateEasyRead from '../../atoms/DateEasyRead/DateEasyRead';

interface IDateFilterProps {
  availableDates: string[];
  activeFilter: string;
  setActiveFilter: (date: string) => void;
}

export default function DateFilter(props: IDateFilterProps) {
  const { availableDates, activeFilter, setActiveFilter } = props;
  return (
    <section className="mb-5 px-6 lg:px-24">
      <div className="flex flex-wrap gap-3 overflow-auto">
        {availableDates.map((date) => (
          <Button
            onClick={() => setActiveFilter(date)}
            display="block"
            color={activeFilter === date ? 'primary' : 'secondary'}
            className={`${
              activeFilter === date
                ? 'text-white'
                : 'border border-primary text-primary'
            } flex-shrink-0`}
          >
            <DateEasyRead date={date} noNeedTime />
          </Button>
        ))}
      </div>
    </section>
  );
}
