import { useState } from 'react';
import { Helmet } from 'react-helmet';
import ivfAcademyData from '../../../data/ivf-academy';
import { titlePageGenerator } from '../../../utils';
import Gap from '../../atoms/Gap/Gap';
import Heading2 from '../../atoms/Heading2/Heading2';
import AnimatedPage from '../../containers/AnimatedPage/AnimatedPage';
import Footer from '../../molecules/Footer/Footer';
import Header from '../../molecules/Header/Header';
import DateFilter from '../../organisms/DateFilter/DateFilter';
import IVFAcademyListGroup from '../../organisms/IVFAcademyListGroup/IVFAcademyListGroup';

function IVFAcademyPage() {
  const filterDates = ivfAcademyData.map((item) => item.date);
  const availableDates = [...new Set(filterDates)];
  const [activeFilter, setActiveFilter] = useState(availableDates[0] || '');
  return (
    <>
      <Helmet>
        <title>{titlePageGenerator('IVF Academy')}</title>
      </Helmet>
      <Header active="/ivf-academy" />
      <AnimatedPage>
        <main>
          <Gap className="h-6" />
          <Heading2 title="IVF Academy" />
          <DateFilter
            activeFilter={activeFilter}
            availableDates={availableDates}
            setActiveFilter={setActiveFilter}
          />
          <IVFAcademyListGroup
            activeFilter={activeFilter}
            webinarList={ivfAcademyData}
          />
          <Footer isEmployer={false} />
        </main>
      </AnimatedPage>
    </>
  );
}

export default IVFAcademyPage;
