import { IWebinarCard } from '../../../interfaces';
import DateEasyRead from '../../atoms/DateEasyRead/DateEasyRead';
import LinkTo from '../../atoms/LinkTo/LinkTo';

interface Props {
  ctaColor: 'primary' | 'warning';
  webinarCard?: IWebinarCard;
}

export default function IVFWebinarCard(props: Props) {
  const { ctaColor, webinarCard } = props;
  if (webinarCard) {
    return (
      <div className="flex max-h-max flex-col justify-between rounded-xl border p-3 font-primary shadow duration-150 hover:-translate-y-1 hover:shadow-lg">
        <h4 className="mb-2 font-semibold">{webinarCard.title}</h4>
        <div className="flex gap-2">
          <img
            src={webinarCard.speaker?.photo}
            className="h-12 w-12 flex-shrink-0 rounded-full object-cover"
            alt=""
          />
          <div className="text-sm">
            <p className="font-medium">{webinarCard.speaker.name}</p>
            <p>{webinarCard.speaker?.profession}</p>
            <p>{webinarCard.speaker?.company}</p>
          </div>
        </div>
        <div
          className={`mt-2 flex items-center justify-between rounded-xl ${
            ctaColor === 'primary' ? 'bg-primary' : 'bg-warning'
          } p-3 py-5 ${
            ctaColor === 'primary' ? 'text-white' : 'text-primary'
          }  `}
        >
          <div>
            <DateEasyRead date={webinarCard.date} noNeedTime newLine />
          </div>
          <div>
            <LinkTo
              className={`rounded-lg p-2 text-sm hover:shadow-lg ${
                ctaColor === 'primary'
                  ? 'border hover:border-warning hover:bg-warning hover:text-primary'
                  : 'border border-primary hover:border-primary hover:bg-primary hover:text-warning'
              }`}
              href="/"
            >
              Daftar
            </LinkTo>
          </div>
        </div>
      </div>
    );
  }
  return (
    <div className="rounded-xl border p-3 font-primary shadow duration-150 hover:-translate-y-1 hover:shadow-lg">
      <h4 className="mb-2 font-semibold">
        Cara Memperoleh Kerja, Seluk Beluk Dunia Kerja
      </h4>
      <div className="flex gap-2">
        <img
          src="https://dummyimage.com/50x50/000000/fff"
          className="h-12 w-12 flex-shrink-0 rounded-full object-cover"
          alt=""
        />
        <div className="text-sm">
          <p className="font-medium">Aditiyo Indrasanto</p>
          <p>Career Consultant</p>
          <p>Sekolah Karir</p>
        </div>
      </div>
      <div
        className={`mt-2 flex items-center justify-between rounded-xl ${
          ctaColor === 'primary' ? 'bg-primary' : 'bg-warning'
        } p-3 ${ctaColor === 'primary' ? 'text-white' : 'text-primary'}  `}
      >
        <div>
          <p>Senin</p>
          <p>04 Oktober 2021</p>
        </div>
        <div>
          <LinkTo
            className={`rounded-lg p-2 text-sm hover:shadow-lg ${
              ctaColor === 'primary'
                ? 'border hover:border-warning hover:bg-warning hover:text-primary'
                : 'border border-primary hover:border-primary hover:bg-primary hover:text-warning'
            }`}
            href="/"
          >
            Daftar
          </LinkTo>
        </div>
      </div>
    </div>
  );
}
